@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
* {
  box-sizing: border-box;
}
*::selection {
  background-color: #0090e0;
  color: #DFE1E5;
}

body {
  margin: 0 auto;
  font-family: Arial, sans-serif;
  color: #DFE1E5;
  font-size: 16px;
  background-image: url("../images/body_background.jpg");
  background-position-y: 100vh;
  background-size: 100%;
  background-repeat: repeat-y;
  min-height: 100vh;
}
body::-webkit-scrollbar {
  width: 10px;
  background-color: #2d2d2d;
}
body::-webkit-scrollbar-thumb {
  background-color: #999;
  border-radius: 5px;
}

.header, .footer {
  background: rgba(0, 0, 0, 0.55);
  backdrop-filter: blur(15px);
  height: 70px;
  box-shadow: 0px 8px 40px black;
}
.header .container, .footer .container {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  min-height: 70px;
  padding: 5px 16px;
}

.header {
  width: 100%;
  position: fixed;
  z-index: 999;
}
.header .container {
  display: flex;
  justify-content: space-between;
}
.header .container .header__logo {
  font-size: 24px;
  text-shadow: 0 20px 50px black;
  background: linear-gradient(90deg, #e933ff, #00a0ff, #7700d5, #0021ff, #3574F0);
  background-size: 500%;
  -webkit-background-clip: text;
  color: rgba(255, 255, 255, 0.1);
  animation: gradient 10s linear infinite;
  font-weight: 700;
}
.header .container .header__avatar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header .container .header__avatar .avatar__image {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  cursor: pointer;
  object-fit: cover;
}
.header .container .nav .nav__link {
  margin: 0 8px;
}
.header .container .nav__user-actions .nav__link {
  margin: 0 8px;
}

.header__bottomMenu {
  width: 1167px;
  margin: 0 auto;
  display: flex;
  justify-content: end;
}
.header__bottomMenu .menu {
  top: 70px;
  position: fixed;
  z-index: 998;
  flex-direction: column;
  justify-content: end;
  box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.35);
  background: rgba(0, 0, 0, 0.55);
  backdrop-filter: blur(15px);
  padding: 8px;
  border-radius: 0 0 8px 8px;
}
.header__bottomMenu .menu .menu__action {
  display: block;
  padding: 8px 0;
  border-bottom: 1px solid #4E5157;
}
.header__bottomMenu .menu .menu__action:last-child {
  border: none;
}

.footer__description {
  margin: 0 auto;
}

.main {
  min-height: 92vh;
  width: 1200px;
  margin: 0 auto;
  padding: 70px 16px 16px;
  display: flex;
  flex-direction: column;
}
.main .catalog {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 16px;
}
.main .catalog .catalog__title {
  grid-column: span 3;
  margin-bottom: 0;
}
.main .catalog .service {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(30, 30, 30, 0.5);
  padding: 16px;
  backdrop-filter: blur(15px);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
}
.main .catalog .service .service__title {
  margin: 0;
  padding-bottom: 16px;
  border-bottom: 2px solid #2d2d2d;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main .catalog .service .service__description {
  margin: 16px 0;
  color: #B4B8BF;
}
.main .catalog .service .service__cost {
  margin: 16px 0;
  color: #868A91;
}
.main .catalog .service .service__actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main .catalog .service .service__actions .service__count {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95px;
  margin: 0;
}
.main .catalog .service .service__actions .service__count .count__setCount {
  height: 32px;
  width: 32px;
  padding: 3px;
  cursor: pointer;
  background-color: #B4B8BF;
  color: #2d2d2d;
  border-radius: 50px;
  transition: 0.3s;
}
.main .releases {
  display: grid;
  justify-content: space-between;
  align-items: start;
  gap: 16px;
}
.main .releases .releases__title {
  grid-column: span 3;
  margin-bottom: 0;
}
.main .releases .release {
  padding: 16px;
  background-color: rgba(30, 30, 30, 0.5);
  backdrop-filter: blur(15px);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
}
.main .releases .release .release__title {
  font-size: 20px;
  margin: 0;
  padding-bottom: 16px;
}
.main .releases .release .release__image {
  border-radius: 8px;
}
.main .releases .release .release__date {
  margin: 8px 0 0;
  color: #868A91;
}
.main .releases .release:first-child {
  margin-left: 0;
}
.main .releases .release:last-child {
  margin-right: 0;
}
.main .releases:before {
  position: absolute;
  width: 100vw;
  height: 70px;
  background: linear-gradient(black, rgba(0, 0, 0, 0));
}
.main .news .news__article {
  background-color: rgba(33, 33, 33, 0.35);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
  padding: 16px;
}
.main .news .news__article .article__image {
  width: 100%;
  border-radius: 12px;
}
.main .news .news__article .article__date {
  margin-top: 5px;
  color: #868A91;
}
.main .authForm {
  font-size: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-column: span 2;
  height: 86vh;
}
.main .shopCart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 50vh;
}
.main .shopCart .shop-cart__empty,
.main .shopCart .shop-cart__items {
  background: rgba(30, 30, 30, 0.5);
  backdrop-filter: blur(15px);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
}
.main .shopCart .shop-cart__items {
  padding: 16px 32px;
}
.main .shopCart .shop-cart__items .item {
  padding: 16px 0;
  border-bottom: 2px solid #2d2d2d;
}
.main .shopCart .shop-cart__items .item:last-child {
  border: none;
}
.main .shopCart .shop-cart__items .item .item__title {
  display: flex;
  align-items: center;
  margin: 0;
}
.main .shopCart .shop-cart__items .item .item__title .span__border {
  padding: 0 6px;
}
.main .shopCart .shop-cart__items .item .item__icon {
  margin-right: 16px;
}
.main .shopCart .shop-cart__items .item .item__description {
  color: #B4B8BF;
}
.main .shopCart .shop-cart__items .item .item__bottom-menu {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.main .shopCart .shop-cart__items .item .item__bottom-menu .item__cost {
  color: #868A91;
  margin: 0;
}
.main .shopCart .shop-cart__items .item .item__bottom-menu .item__actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.main .shopCart .shop-cart__items .item .item__bottom-menu .item__actions .action {
  margin: 0 8px;
}
.main .shopCart .shop-cart__items .item .item__bottom-menu .item__actions > * {
  cursor: pointer;
}
.main .shopCart .shop-cart__empty {
  padding: 16px;
}
.main .shopCart .shop-cart__empty .empty__title {
  margin: 0 0 16px;
}
.main .shopCart .shop-cart__bottomMenu {
  display: flex;
  align-items: center;
  margin-top: 16px;
}
.main .shopCart .shop-cart__bottomMenu .shop-cart__sum {
  margin: 0 32px 0 0;
}
.main .aboutUs {
  display: flex;
  flex-direction: column;
}
.main .aboutUs > * {
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
}
.main .aboutUs .aboutUs__article {
  background: rgba(30, 30, 30, 0.5);
  backdrop-filter: blur(15px);
  border-radius: 16px;
  margin-bottom: 16px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.main .aboutUs .aboutUs__article:last-child {
  margin-bottom: 0;
}
.main .aboutUs .aboutUs__article .about-us__paragraph {
  padding: 16px;
  display: block;
  font-size: 20px;
  line-height: 28px;
  margin: 0;
}
.main .aboutUs .aboutUs__article .about-us__image {
  width: 100%;
  height: 100%;
}
.main .aboutUs .aboutUs__article .about-us__image_left {
  border-radius: 16px 0 0 16px;
}
.main .aboutUs .aboutUs__article .about-us__image_right {
  border-radius: 0 16px 16px 0;
}
.main .orders .order {
  padding: 16px 16px 0;
  background-color: rgba(30, 30, 30, 0.5);
  backdrop-filter: blur(15px);
  border-radius: 16px;
  margin: 16px 0;
}
.main .orders .order .order__title {
  font-size: 20px;
  margin: 0;
  border-bottom: 2px solid #2d2d2d;
  padding-bottom: 16px;
}
.main .orders .order .order__service {
  border-bottom: 2px solid #2d2d2d;
  margin-top: 16px;
}
.main .orders .order .order__service:last-child {
  border-bottom: none;
}
.main .orders .order .order__service .service__title {
  color: #DFE1E5;
}
.main .orders .order .order__service .service__description {
  color: #B4B8BF;
}
.main .orders .order .order__service .service__cost {
  color: #868A91;
  margin-bottom: 0;
  padding-bottom: 16px;
}
.main .orders .order .order__sum {
  margin-bottom: 0;
  padding-bottom: 16px;
  color: #868A91;
}
.main .userProfile {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: min-content;
  justify-items: stretch;
  gap: 16px;
}
.main .userProfile .userProfile__item {
  background: rgba(30, 30, 30, 0.5);
  backdrop-filter: blur(15px);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
  width: 100%;
  padding: 16px 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.main .userProfile .userProfile__item .item__title {
  margin: 0;
  padding-bottom: 16px;
  border-bottom: 2px solid #2d2d2d;
}
.main .userProfile .userProfile__item .item__information {
  display: flex;
  flex-direction: column;
  justify-content: start;
  padding-bottom: 0;
  height: border-box;
}
.main .userProfile .userProfile__item .item__information .information__general {
  margin-bottom: 0;
  color: #B4B8BF;
}
.main .userProfile .userProfile__item .item__information .information__secondary {
  color: #868A91;
}
.main .userProfile .userProfile__item .item__information .item__action {
  margin: 0;
}
.main .userProfile .userProfile__item .item__information .miniItems {
  margin-top: 16px;
  display: grid;
  gap: 16px;
  grid-template-columns: auto auto;
}
.main .userProfile .userProfile__item .item__information .miniItems.miniItems_vertical {
  grid-template-columns: auto;
}
.main .userProfile .userProfile__item .item__information .miniItems .miniItem {
  background-color: rgba(30, 30, 30, 0.7);
  padding: 16px;
  border-radius: 16px;
  width: fit-content;
  position: relative;
}
.main .userProfile .userProfile__item .item__information .miniItems .miniItem.miniItem_horizontal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 272px;
}
.main .userProfile .userProfile__item .item__information .miniItems .miniItem .miniItem__title {
  margin-top: 0;
  margin-bottom: 16px;
}
.main .userProfile .userProfile__item .item__information .miniItems .miniItem .miniItem__block {
  text-align: justify;
  min-height: 200px;
  margin-bottom: 16px;
}
.main .userProfile .userProfile__item .item__information .miniItems .miniItem .miniItem__secondary {
  margin: 8px 0;
}
.main .userProfile .userProfile__item .item__information .miniItems .miniItem .miniItem__edit {
  position: absolute;
  right: 25px;
  bottom: 25px;
}
.main .userProfile .userProfile__item .item__information .miniItems_vertical .miniItem {
  display: flex;
  width: 100%;
}
.main .userProfile .userProfile__item .item__information .miniItems_vertical .miniItem__aside {
  margin-left: 16px;
  width: 100%;
}
.main .userProfile .userProfile__item .item__information .miniItems_vertical .miniItem__aside .aside__item {
  display: flex;
  justify-content: space-between;
}
.main .userProfile .userProfile__item .item__information .miniItems_vertical .miniItem__aside .aside__item .item__description {
  margin: 4px 0;
}
.main .userProfile .item__artistData {
  grid-row: span 2;
  height: 100%;
}
.main .userProfile .item__artistData .artistData__profileImage {
  border-radius: 16px;
  margin: 0;
  display: block;
  width: 200px;
  height: 200px;
}
.main .userProfile .item__artistData .artistData__bio {
  line-height: 22px;
}
.main .userProfile .item__userData .userData__userEmail {
  margin-bottom: 0;
}
.main .userProfile .item__userData .userData__editableItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 12px;
  height: 28px;
}
.main .userProfile .item__userData .userData__editableItem .editableItem__title {
  margin-top: 0;
}
.main .userProfile .item__userData .userData__form {
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.main .userProfile .item__userData .userData__form .userData__input, .main .userProfile .item__userData .userData__form .userData__label, .main .userProfile .item__userData .userData__form .userData__button {
  height: 28px;
}
.main .userProfile .item__releasesData {
  grid-row: span 4;
}
.main .userProfile .item__releasesData .releaseData__releaseImage {
  width: 200px;
  height: 200px;
  display: block;
  border-radius: 16px;
}

.helloElement {
  background-image: url("../images/background.jpg");
  background-size: cover;
  background-position-x: 50vw;
  background-repeat: repeat-x;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.helloElement .container {
  width: 1100px;
}
.helloElement .container .helloElement__title {
  font-size: 65px;
  text-align: center;
  text-shadow: 0 0 20px black;
  color: #DFE1E5;
  margin-bottom: 50px;
}
.helloElement .container .helloElement__description {
  font-size: 24px;
  text-align: center;
  background: linear-gradient(90deg, #e933ff, #00a0ff, #7700d5, #0021ff, #3574F0);
  background-size: 200%;
  -webkit-background-clip: text;
  color: transparent;
  animation: gradient 4s linear infinite;
}

.notification_hide {
  opacity: 0;
}

.notification_show {
  opacity: 1;
}

.notifications {
  position: fixed;
  right: 16px;
  bottom: 0;
  z-index: 9999;
}
.notifications .notification {
  margin: 16px 0;
  transition: 0.5s;
  background: rgba(30, 30, 30, 0.5);
  backdrop-filter: blur(15px);
  padding: 16px;
  width: 390px;
  border-radius: 12px;
}
.notifications .notification .notification__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
}
.notifications .notification .notification__title .notification__close {
  cursor: pointer;
  color: #DB5C5C;
}

a {
  color: #6B9BFA;
  text-decoration: none;
  transition: 0.3s;
  cursor: pointer;
}
a:hover {
  color: #366ACF;
}
a:active {
  color: #375FAD;
}

.button {
  color: #DFE1E5;
  background: #3574F0;
  cursor: pointer;
  border: none;
  padding: 6px 15px;
  height: 32px;
  border-radius: 4px;
  transition: 0.3s;
}
.button:hover {
  opacity: 90%;
}
.button:active {
  opacity: 75%;
}

.button__success {
  height: fit-content;
  background-color: #57965C;
  color: #DFE1E5;
  padding: 8px 16px;
  border: none;
  border-radius: 100px;
  cursor: pointer;
  transition: 0.2s;
  width: fit-content;
}

.icon {
  height: 28px !important;
  width: 28px !important;
}
.icon.icon_edit {
  color: #A571E6;
}

.form .form__label {
  color: #B4B8BF;
}
.form .form__input {
  box-sizing: border-box;
  width: 300px;
  margin: 8px 0;
  font-size: 18px;
  border: 2px solid #4E5157;
  border-radius: 4px;
  padding: 8px 6px;
  color: #DFE1E5;
  background-color: #2d2d2d;
}
.form .form__input:focus {
  border-color: #3574F0;
  outline: none !important;
}
.form .form__input_submit {
  cursor: pointer;
  color: #DFE1E5;
  border: none;
  background-color: #3574F0;
  transition: 0.2s;
}
.form .form__input_submit:hover {
  background-color: #366ACF;
}
.form .form__input_submit:active {
  background-color: #375FAD;
}
.form .input {
  height: 28px;
  border-radius: 4px;
  padding: 6px 9px;
  background: none;
  color: #B4B8BF;
  border: 2px solid #868A91;
}
.form .label {
  color: #868A91;
}

.block-title {
  background-color: rgba(30, 30, 30, 0.5);
  backdrop-filter: blur(15px);
  padding: 16px;
  margin: 32px 0 16px;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
}

.span__border {
  font-weight: 400;
}

.actions__delete {
  color: #DB5C5C;
}

.actions__edit {
  color: #5FAD65;
}

.icon {
  transition: 0.3s !important;
  cursor: pointer;
}
.icon:hover {
  filter: opacity(70%);
}
.icon:active {
  filter: opacity(50%);
}

